.loading-card-overlay {
  position: absolute;
  inset: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.7);
  color: white;
  border-radius: 0.5rem;
  z-index: 10;
}

.loading-card-text {
  font-size: 1.5rem;
  font-weight: bold;
}
